html {
  box-sizing: border-box;
  min-height: 1810px;
  max-height: 1930px;
  font-family: TTLakes, TTLakes-Bold, TTLakes-Regular;
}


@media(min-width:768px ){
  html {
    min-height: 1857px;
    max-width: 2300px;
  }
  
}


@media(min-width:1440px ){
  html {
   display: flex;
   flex-direction: column;
min-height: 1990px;
max-height: 2400px;
  }
  
}

*,
*:before,
*:after {
  box-sizing: inherit;
}
